import { Injectable, ErrorHandler } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { errorMessage } from '../shared/app.constants'
import { ToastrManager } from 'ng6-toastr-notifications';
@Injectable()
export class ErrorLogService extends ErrorHandler {

  constructor(public toastr: ToastrManager) {
    super();
  }

  // handleError(error): void {
  //   this.errorLogService.logError(error);
  // }

  handleError(error: any) {
    if (error instanceof HttpErrorResponse) {
      if (typeof error.error.message !== 'undefined') {
        this.toastr.errorToastr(error.error.message);
      } else if (typeof error.message !== 'undefined') {
        if (error.status === 404) {
          this.toastr.errorToastr('404: ' + errorMessage.pageNotFound);
        } else if (error.status === 500) {
          this.toastr.errorToastr('500: ' + errorMessage.internalServerError);
        } else if (error.status === 403) {
          this.toastr.errorToastr('403: ' + errorMessage.forbidden);
        } else if (error.status === 0) {
          this.toastr.errorToastr('0: ' + errorMessage.unknownError);
        } else {
          this.toastr.errorToastr(error.message);
        }
      } else if (typeof error.status !== 'undefined') {
        this.toastr.errorToastr(error.status.toString());
      } else {
        this.toastr.errorToastr(errorMessage.httpError);
      }
      // console.error(date, AppConstants.httpError, error.message, 'Status code:',
      // (<HttpErrorResponse>error).status);
    }
    else if (error instanceof TypeError) {
      if (typeof error.message !== 'undefined') {
        this.toastr.errorToastr(error.message);
      } else {
        this.toastr.errorToastr(errorMessage.typeError);
      }
      // console.error(date, AppConstants.typeError, error.message, error.stack);
    }
    else if (error instanceof Error) {
      if (typeof error.message !== 'undefined') {
        this.toastr.errorToastr(error.message);
      } else {
        this.toastr.errorToastr(errorMessage.generalError);
      }
      // console.error(date, AppConstants.generalError, error.message, error.stack);
    }
    else if (error instanceof ErrorEvent) {
      if (typeof error.message !== 'undefined') {
        this.toastr.errorToastr(error.message);
      } else {
        this.toastr.errorToastr(errorMessage.generalError);
      }
      //A client-side or network error occurred. Handle it accordingly.
      // console.error(date, AppConstants.generalError, error.message);
    }
    else {
      if (typeof error !== 'undefined') {
        this.toastr.errorToastr(error);
      } else {
        this.toastr.errorToastr(errorMessage.somethingWrong);
      }
      // console.error(date, AppConstants.somethingHappened, error.message, error.stack);
    }
  }
  handleSuccess(message: any) {
    this.toastr.successToastr(message);
  }
}