import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { ICommon } from '../shared/interfaces';
import { ErrorLogService } from './error-log.service';
import { createUrl } from "../shared/app.constants";
@Injectable({
    providedIn: 'root'
})
export class CommonService {

    constructor(
        private http: HttpClient,
        private errorLogService: ErrorLogService) {
    }

    getData(method: string): Observable<ICommon> {
        return this.http.get<ICommon>(createUrl(method))
            .pipe(
                catchError(this.handleError.bind(this))
            );
    }

    addData(method, data): Observable<ICommon> {
        return this.http.post<ICommon>(createUrl(method), data).pipe(
            map((response) => {
                return response;
            }),
            catchError(this.handleError.bind(this))
        );
    }

    updateData(method, data): Observable<ICommon> {
        return this.http.put<ICommon>(createUrl(method), data)
            .pipe(
                map((response) => {
                    return response;
                }),
                catchError(this.handleError.bind(this))
            );
    }

    deleteData(method): Observable<ICommon> {
        return this.http.delete<ICommon>(createUrl(method))
            .pipe(
                catchError(this.handleError.bind(this))
            );
    }
    private handleError(error) {
        this.errorLogService.handleError(error);
    }

}
