import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ErrorLogService } from './error-log.service';
import { ILogin } from '../shared/interfaces';
import { appApi } from "../shared/app.constants";
@Injectable({
  providedIn: 'root'
})
export class UserService {

  private loggedInStatus = JSON.parse(localStorage.getItem('loggedIn') || 'false');
  constructor(
    private http: HttpClient,
    private router: Router,
    private errorLogService: ErrorLogService) { }

  login(data) {
    // let input = new FormData();
    // for (var key in data) {
    //   if (typeof data[key] == 'object') {
    //     var details = JSON.stringify(data[key]);
    //     input.append(key, details);
    //   } else {
    //     input.append(key, data[key]);
    //   }
    // }
    return this.http.post<ILogin>(appApi.login, data).subscribe(
      (result) => {
        if (result.status) {
          localStorage.setItem('loggedIn', 'true');
          // localStorage.setItem('login_token', result.data.login_token);
          localStorage.setItem('user_id', result.data._id);
          // localStorage.setItem('role_id', result.data.role_id);
          // localStorage.setItem('device_id', '192.168.0.114');
          localStorage.setItem('fullname', result.data.firstname + ' ' + result.data.lastname);
          this.errorLogService.handleSuccess(result.message);
          this.router.navigate(['/'])
        } else {
          this.errorLogService.handleError(result.message)
        }
      },
      (error) => this.handleError(error)
    );

  }

  get isLoggedIn() {
    return JSON.parse(localStorage.getItem('loggedIn') || this.loggedInStatus.toString())
  }

  logout() {
    return this.http.post<ILogin>(appApi.logout, {}).subscribe(
      (result) => {
        if (result.status) {
          localStorage.setItem('loggedIn', 'false');
          // localStorage.setItem('login_token', 'false');
          localStorage.setItem('user_id', 'false');
          // localStorage.setItem('role_id', 'false');
          // localStorage.setItem('device_id', 'false');
          localStorage.setItem('fullname', 'false');
          this.router.navigate(['/login'])
        } else {
          this.errorLogService.handleError(result.message)
        }
      },
      (error) => this.handleError(error)
    );
  }

  forgotPassword(data) {
    return this.http.post<ILogin>('auth/forgot_password', data).subscribe(
      (result) => {
        if (result.status) {
          this.errorLogService.handleSuccess(result.message);
          this.router.navigate(['/login'])
        } else {
          this.errorLogService.handleError(result.message)
        }
      },
      (error) => this.handleError(error)
    );
  }

  changePassword(data) {
    return this.http.post<ILogin>('auth/change_password', data).subscribe(
      (result) => {
        if (result.status) {
          this.errorLogService.handleSuccess(result.message);
          this.router.navigate(['/'])
        } else {
          this.errorLogService.handleError(result.message)
        }
      },
      (error) => this.handleError(error)
    );
  }

  public roleCheck(role_id): boolean {
    var isMatch = false;
    var userRole: string[] = JSON.parse(localStorage.getItem('role_id'));
    role_id.forEach(element => {
      if (userRole.toString().indexOf(element) > -1) {
        // console.log(userRole.toString().indexOf(element));
        isMatch = true;
        return false;
      }
    });
    return isMatch;
  }

  private handleError(error) {
    this.errorLogService.handleError(error);
  }

}
