import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { CommonService } from '../../services/common.service';
import { ErrorLogService } from '../../services/error-log.service';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {

  title = 'Report';
  reportForm: FormGroup;
  PatientID: string;
  patientDetails: [];
  reportDetails: [];
  PatientDetails: any;
  ReportDetails: any;
  PatientName: '';
  Study: 'Not available';
  StudyDate: '-';
  currentDate = new Date();
  public preview: boolean = false;
  public previewButton: any = 'Preview';
  public show: boolean = false;
  public buttonName: any = 'Edit';

  constructor(
    private titleService: Title,
    private commonService: CommonService,
    private errorLogService: ErrorLogService,
    private route: ActivatedRoute,
    private router: Router,
    public formBuilder: FormBuilder,
  ) {
    this.titleService.setTitle('Report');
  }
  ngOnInit() {
    this.PatientID = this.route.snapshot.paramMap.get("id");
    this.reportForm = this.formBuilder.group({
      _id: [this.PatientID],
      PatientDetails: this.formBuilder.array([]),
      ReportDetails: this.formBuilder.array([])
    });
    this.index();
  }
  index() {
    this.commonService.getData('study/' + this.PatientID).subscribe(
      response => {
        if (response.status) {
          this.PatientDetails = Object.keys(response.data.PatientDetails).map(key => ({ objectkey: key, value: response.data.PatientDetails[key].value, key: response.data.PatientDetails[key].key }));
          this.PatientDetails.forEach(object => {
            if (object.objectkey === 'PatientName') {
              this.PatientName = object.value;
            }
            if (object.objectkey === 'StudyDescription') {
              this.Study = object.value;
            }
            if (object.objectkey === 'StudyDate') {
              this.StudyDate = object.value;
            }
            (<FormArray>this.reportForm.get('PatientDetails')).push(this.formBuilder.group({
              key: [object.key],
              value: [object.value]
            }));
            // this.reportForm.addControl(object.key, this.formBuilder.control(object.value, Validators.required));
          });
          this.patientDetails = response.data.PatientDetails;
          // console.log(this.patientDetails);

          if (response.data.ReportDetails !== undefined) {
            this.ReportDetails = Object.keys(response.data.ReportDetails).map(key => ({ objectkey: key, value: response.data.ReportDetails[key].value, key: response.data.ReportDetails[key].key }));
            this.ReportDetails.forEach(object => {
              (<FormArray>this.reportForm.get('ReportDetails')).push(this.formBuilder.group({
                key: [object.key],
                value: [object.value]
              }));
            });
            this.reportDetails = response.data.ReportDetails;
          }
        } else {
          this.errorLogService.handleError(response.message)
          this.router.navigate(['/']);
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  patientDetailsSubmit() {
    // var PatientDetails = {};
    // this.allDetails.forEach(object => {
    //   PatientDetails[object.objectkey] = { 'key': object.key, 'value': this.reportForm.controls[object.objectkey].value };
    // });
    // var NewField = this.reportForm.get('NewField').value;
    // var ExtraPatientDetails = {};
    // NewField.forEach(object => {
    //   ExtraPatientDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
    // });
    // const AllDetails = Object.assign({}, PatientDetails, ExtraPatientDetails);
    var PatientDetailsValue = this.reportForm.get('PatientDetails').value;
    var ExtraPatientDetails = {};
    PatientDetailsValue.forEach(object => {
      if (object.key && object.value) {
        ExtraPatientDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
      }
    });
    this.commonService.updateData('study/edit/' + this.reportForm.controls['_id'].value, { 'PatientDetails': ExtraPatientDetails }).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          // this.router.navigate(['/']);
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  reportDetailsSubmit() {
    var ReportDetailsValue = this.reportForm.get('ReportDetails').value;
    var ExtraPatientDetails = {};
    ReportDetailsValue.forEach(object => {
      if (object.key && object.value) {
        ExtraPatientDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
      }
    });
    this.commonService.updateData('study/edit-report/' + this.reportForm.controls['_id'].value, { 'ReportDetails': ReportDetailsValue }).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          // this.router.navigate(['/']);
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  addField(): void {
    const arrayControl = <FormArray>this.reportForm.get('PatientDetails');
    arrayControl.push(this.formBuilder.group({
      key: [''],
      value: ['']
    }));
  }
  removeField(index: number): void {
    const arrayControl = <FormArray>this.reportForm.get('PatientDetails');
    arrayControl.removeAt(index);
    this.patientDetailsSubmit();
  }
  addDetails(): void {
    const arrayControl = <FormArray>this.reportForm.get('ReportDetails');
    arrayControl.push(this.formBuilder.group({
      key: [''],
      value: ['']
    }));
  }
  removeDetails(index: number): void {
    const arrayControl = <FormArray>this.reportForm.get('ReportDetails');
    arrayControl.removeAt(index);
    this.reportDetailsSubmit();
  }
  editToggle() {
    this.show = !this.show;
    if (this.show)
      this.buttonName = "Done";
    else
      this.buttonName = "Edit";
  }
  previewToggle() {
    this.preview = !this.preview;
    if (this.preview)
      this.previewButton = "Edit Report";
    else
      this.previewButton = "Preview";
  }
  dropPatientDetails(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.reportForm.get('PatientDetails')['controls'], event.previousIndex, event.currentIndex);
    moveItemInArray(this.reportForm.get('PatientDetails').value, event.previousIndex, event.currentIndex);
    this.patientDetailsSubmit();
  }
  dropReportDetails(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.reportForm.get('ReportDetails')['controls'], event.previousIndex, event.currentIndex);
    moveItemInArray(this.reportForm.get('ReportDetails').value, event.previousIndex, event.currentIndex);
    this.reportDetailsSubmit();
  }

}
